.back_dop-fillter {
    width: 100%;
    height: 100vh;
    position: relative;
    z-index: 1;
    background-image: url("../../assets/image-logo/backround-loading.jpg");
    .wrap_loading-start {
        width: 100%;
        height: 100%;
        @include styleDisFlexCenter;
        background-color: #00000038;
        backdrop-filter: blur(6px);
        .text {
            // color: red;
            // .name_heading {
            //     font-size: 50px;
            //     font-weight: 700;
            //     color: red;
            // }
            // .name_bicyi {
            //     font-size: 15px;
            //     color: red;
            // }
            img{
                width: 350px;
            }
        }
        .stage {
            width: 100%;
            @include styleDisFlexCenter;
            margin-top: 70px;
            .dot-spin {
                position: relative;
                width: 10px;
                height: 10px;
                border-radius: 10px;
                background-color: transparent;
                color: transparent;
                box-shadow: 0 -18px 0 0 #ffffff, 12.727926px -12.727926px 0 0 #ffffff, 18px 0 0 0 #ffffff,
                    12.727926px 12.727926px 0 0 #9880ff00, 0 18px 0 0 #9880ff00,
                    -12.727926px 12.727926px 0 0 #9880ff00, -18px 0 0 0 #9880ff00,
                    -12.727926px -12.727926px 0 0 #9880ff00;
                animation: dot-spin 1.2s infinite linear;
            }

            @keyframes dot-spin {
                0%,
                100% {
                    box-shadow: 0 -18px 0 0 #ffffff, 12.727926px -12.727926px 0 0 #ffffff, 18px 0 0 0 #ffffff,
                        12.727926px 12.727926px 0 -5px #9880ff00, 0 18px 0 -5px #9880ff00,
                        -12.727926px 12.727926px 0 -5px #9880ff00, -18px 0 0 -5px #9880ff00,
                        -12.727926px -12.727926px 0 -5px #9880ff00;
                }
                12.5% {
                    box-shadow: 0 -18px 0 -5px #9880ff00, 12.727926px -12.727926px 0 0 #ffffff,
                        18px 0 0 0 #ffffff, 12.727926px 12.727926px 0 0 #ffffff, 0 18px 0 -5px #9880ff00,
                        -12.727926px 12.727926px 0 -5px #9880ff00, -18px 0 0 -5px #9880ff00,
                        -12.727926px -12.727926px 0 -5px #9880ff00;
                }
                25% {
                    box-shadow: 0 -18px 0 -5px #9880ff00,
                        12.727926px -12.727926px 0 -5px #9880ff00, 18px 0 0 0 #ffffff,
                        12.727926px 12.727926px 0 0 #ffffff, 0 18px 0 0 #ffffff,
                        -12.727926px 12.727926px 0 -5px #9880ff00, -18px 0 0 -5px #9880ff00,
                        -12.727926px -12.727926px 0 -5px #9880ff00;
                }
                37.5% {
                    box-shadow: 0 -18px 0 -5px #9880ff00,
                        12.727926px -12.727926px 0 -5px #9880ff00, 18px 0 0 -5px #9880ff00,
                        12.727926px 12.727926px 0 0 #ffffff, 0 18px 0 0 #ffffff, -12.727926px 12.727926px 0 0 #ffffff,
                        -18px 0 0 -5px #9880ff00, -12.727926px -12.727926px 0 -5px #9880ff00;
                }
                50% {
                    box-shadow: 0 -18px 0 -5px #9880ff00,
                        12.727926px -12.727926px 0 -5px #9880ff00, 18px 0 0 -5px #9880ff00,
                        12.727926px 12.727926px 0 -5px #9880ff00, 0 18px 0 0 #ffffff,
                        -12.727926px 12.727926px 0 0 #ffffff, -18px 0 0 0 #ffffff,
                        -12.727926px -12.727926px 0 -5px #9880ff00;
                }
                62.5% {
                    box-shadow: 0 -18px 0 -5px #9880ff00,
                        12.727926px -12.727926px 0 -5px #9880ff00, 18px 0 0 -5px #9880ff00,
                        12.727926px 12.727926px 0 -5px #9880ff00, 0 18px 0 -5px #9880ff00,
                        -12.727926px 12.727926px 0 0 #ffffff, -18px 0 0 0 #ffffff, -12.727926px -12.727926px 0 0 #ffffff;
                }
                75% {
                    box-shadow: 0 -18px 0 0 #ffffff, 12.727926px -12.727926px 0 -5px #9880ff00,
                        18px 0 0 -5px #9880ff00, 12.727926px 12.727926px 0 -5px #9880ff00,
                        0 18px 0 -5px #9880ff00, -12.727926px 12.727926px 0 -5px #9880ff00,
                        -18px 0 0 0 #ffffff, -12.727926px -12.727926px 0 0 #ffffff;
                }
                87.5% {
                    box-shadow: 0 -18px 0 0 #ffffff, 12.727926px -12.727926px 0 0 #ffffff,
                        18px 0 0 -5px #9880ff00, 12.727926px 12.727926px 0 -5px #9880ff00,
                        0 18px 0 -5px #9880ff00, -12.727926px 12.727926px 0 -5px #9880ff00,
                        -18px 0 0 -5px #9880ff00, -12.727926px -12.727926px 0 0 #ffffff;
                }
            }
        }
    }
}
