.wrap__home {
    width: 100%;
    .video_trailer {
        width: 100%;
        position: relative;
        top: -20px;
        z-index: 3;
        video {
            width: 100%;
        }
        .box_btn_control--work {
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0px;
            top: 0px;
            padding: 0px 50px;
            z-index: 4;
            margin-top: 50px;
            @include styleDisFlexCenter();
            justify-content: left;
            .size-bozing_control {
                width: 100%;
                display: flex;
                .border__content {
                    width: 700px;
                    img {
                        width: 100%;
                        max-height: 100%;
                    }
                    .row-footer {
                        width: 100%;
                        display: flex;
                        flex-direction: column;
                        .text_introduce_trailer {
                            font-size: 16px;
                            font-weight: 600;
                            margin-top: 30px;
                        }
                    }
                    .block-btn__control {
                        @include styleDisFlexCenter();
                        justify-content: space-around;
                        margin-top: 40px;
                        .button-mixin {
                            border-radius: 5px;
                            @include styleDisFlexCenter;
                            cursor: pointer;
                            transition: all 0.4s;
                            color: #fff;
                            border: 2px solid #ffffff6e;
                            padding: 8px 17px;
                            font-weight: 700;
                            font-size: 19px;
                            box-shadow: #0000005e 2px 0px 7px 2px;
                            animation: an_btn_mixin 0.3s;
                            @keyframes an_btn_mixin {
                                from{
                                    transform: scale(0.8);
                                }
                                to{
                                    transform: scale(1);
                                }
                            }
                            svg {
                                margin-right: 10px;
                            }
                            &:hover {
                                transform: translateY(-3px);
                            }
                        }
                        .border__btn_play--list {
                            background: #ff0000bb;
                            border: none;
                            padding: 12px 22px !important;
                        }
                        .border__btn_play_info {
                            background-color: #9b93937c;
                        }
                    }
                }
                .control_btn_trailer-sion {
                    width: 100%;
                    justify-content: right;
                    position: relative;
                    .tr-control_btn_trailer-sion {
                        width: 100%;
                        @include styleDisFlexCenter();
                        justify-content: right;
                        position: absolute;
                        bottom: 0px;
                        .borderPP-trailer {
                            cursor: pointer;
                            width: 40px;
                            height: 40px;
                            border: 2px solid $main-color-while;
                            border-radius: 100%;
                            @include styleDisFlexCenter();
                            background-color: #ffffff3b;
                            margin-right: 10px;
                            .an_icon_pp-trailer {
                                animation: an_huB 0.5s forwards;
                                @keyframes an_huB {
                                    from {
                                        transform: translateY(-6px);
                                    }
                                    to {
                                        transform: translateY(0px);
                                    }
                                }
                            }
                        }
                    }

                    .border_old_warning {
                        width: 43px;
                        height: 43px;
                        border: 2px solid $main-color-while;
                        border-radius: 100%;
                        font-weight: 600;
                        @include styleDisFlexCenter();
                        background-color: #ff0202af;
                        margin: 0px 0px 0px 15px;
                    }
                }
            }
        }
        .bottom_shadow {
            width: 100%;
            height: 80px;
            box-shadow: inset #000000c9 0px -48px 29px 4px;
            position: absolute;
            bottom: 0px;
            z-index: 7;
        }
    }
    .box_slide-review {
        width: 100%;
        margin-top: -150px;
        position: relative;
        z-index: 8;
        .body_slide-image {
            width: 100%;
            height: 100%;
            .slick-slider {
                width: 100%;
                height: 100%;
                position: relative;
                @include styleDisFlexCenter;
                .slick-list {
                    width: 100%;
                    height: 100%;
                    border-radius: 10px;
                    border: 2px solid #746f6f91;
                    box-shadow: $main-color_box_shadow;
                    .slick-track {
                        position: relative;
                        height: 100%;
                        left: 5px;
                        a {
                            height: 100%;
                            position: relative;
                            outline: none;
                            img {
                                width: 100%;
                                height: 100%;
                                transition: all 0.4s;
                                transform: scale(1.015);
                                &:hover {
                                    transform: scale(1.05);
                                }
                                &:active {
                                    transform: scale(1.1);
                                }
                            }
                        }
                    }
                }
                > span {
                    cursor: pointer;
                    padding: 5px;
                    svg {
                        font-size: 22px;
                    }
                }

                .slick-dots {
                    height: 5px;
                    position: absolute;
                    bottom: 20px;
                    background: none;
                    @include styleDisFlexCenter;
                    display: flex !important;
                    padding: 0px;
                    li {
                        width: 5px;
                        height: 100%;
                        list-style: none;
                        margin: 0px 10px;
                        background-color: #ffffff52;
                        border-radius: 100%;
                        button {
                            display: none;
                        }
                    }
                    .slick-active {
                        background-color: #ffffff;
                    }
                }
            }
        }
    }
    .body_home {
        padding: 40px 50px;

        .render_view_box_channle {
            width: 100%;
            margin-top: 20px;
            .row_heading-line {
                width: 100%;
                margin-bottom: 20px;
                .text_heading-bold {
                    font-size: 35px;
                    font-weight: 600;
                    @include styleDisFlexCenter;
                    justify-content: left;
                    svg {
                        font-size: 50px;
                        margin-right: 10px;
                    }
                }
            }

            .body_tr-view {
                width: 100%;
                @include styleDisFlexCenter;
                flex-wrap: wrap;
                .item_box-view {
                    width: 17%;
                    height: 100%;
                    margin: 10px 15px;
                    position: relative;
                    border-radius: 10px;
                    box-shadow: $main-color_box_shadow;
                    cursor: pointer;
                    overflow: hidden;
                    transition: all 0.4s;
                    border: 2px solid #f9f9f91a;

                    .img-bgr_play {
                        width: 100%;
                        height: 100%;
                        opacity: 0.05;
                        transition: all 0.4s;
                        transform: scale(1.1);
                    }
                    > .box_view-chition {
                        width: 100%;
                        height: 100%;
                        position: absolute;
                        top: 0px;
                        left: 0px;
                        background-color: #222a442f;
                        & > a {
                            width: 100%;
                            height: 100%;
                            @include styleDisFlexCenter;
                            & > img {
                                width: 78%;
                            }
                        }
                    }
                }
                .item_box-view:hover {
                    transform: scale(1.07);
                    border: 2px solid $main-color-while;
                    box-shadow: $main-color_box_shadow-hover;
                    .img-bgr_play {
                        opacity: 1;
                    }
                }
            }
        }
    }
}
.opacity-0 {
    opacity: 0;
}
.opacity-1 {
    opacity: 1;
}
